@import url(https://fonts.googleapis.com/css?family=Lato:300,300i,400,400i,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
    font-family: 'Lato', sans-serif;
    color: rgba(26,26,26,.7);
    box-sizing: border-box;  
    line-height: 1.2;
}

h1{
    font-size: 3rem; 
    text-transform: uppercase;
    letter-spacing: 0.5rem;
}

h2{
    font-size: 2rem;
    text-transform: uppercase;
    font-weight: 400;
    letter-spacing: 0.15rem;
}

h3{
    font-weight: 300;
    font-style: italic;
    font-size: 1.4rem;
}

h4{
    font-weight: 500;
    text-transform: uppercase;
    font-size: 1.3rem;
}

h5{
    font-weight: 400;
    font-style: italic;
    font-size: 0.9rem;
}

a{
    cursor: pointer !important;
}

/* Header CSS */

.header {
    margin: 0;
    top: 0;
    left: 0;
    width: 100vw;
    height: 10vh;
    position: absolute;
    background-color: transparent;
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-content: center; 
  }

.header img{
    -webkit-filter: invert(1);
            filter: invert(1);
    width: 20rem;
    margin-left: 2rem
}

/*HIDDEN on mobile*/ 
.header .desktopMenu{ 
    justify-self: end;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
    margin-right: 2rem;
}

.header .desktopMenu a{
    text-decoration: none;
    text-transform: uppercase;
    font-family: 'Lato', sans-serif;
    font-weight: 400;
    font-size: 1rem;
    letter-spacing: 0.05rem;
    color: white;
    position: relative;
    justify-self: start;
}

.header .desktopMenu a:before {
    content: " ";
    position: absolute;
    width: 100%;
    height: 1px;
    bottom: -5px;
    left: 0;
    background: white;
    visibility: hidden;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transition: .2s linear;
    transition: .2s linear;
}

.desktopMenu a:hover:before, a:focus:before{
    visibility: visible;
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
}

.nav-link--active{
    color: #a7a7a7 !important;
    font-weight: 700 !important;
}

.nav-link--active:hover:before{
    visibility: hidden !important;
}

.headerMobile{
    margin: 0;
    top: 0;
    left: 0;
    width: 100vw;
    height: 10vh;
    position: absolute;
    background-color: transparent;
    display: none;
}

/* Footer CSS */

.footer{
    width: 100vw;
    background-color: #a7a7a7;
    min-height: 50vh;
    color: white;
    padding: 4rem 0 0 0;
}
.footer-columns{
    margin: auto;
    width: 40%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1rem;
    gap: 1rem;
}

.credits h5{
    text-align: center;
    font-size: 0.8rem;
    padding: 2rem;
}

.footer a{
    margin: 0;
    text-transform: capitalize;
    text-decoration: none;
    color: white;
    line-height: 1.5;
    letter-spacing: 0.05rem;
    cursor: pointer;
    justify-self: start;
    position: relative;
    clear: both;
    float: left;
  }

.footer a:before {
    content: " ";
    position: absolute;
    width: 100%;
    height: 1px;
    bottom: 0;
    left: 0;
    background: white;
    visibility: hidden;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transition: .2s linear;
    transition: .2s linear;

}

.footer a:hover{
    color: white !important;
    text-decoration: none !important;
}

.footer-menu a:hover:before, a:focus:before {
    visibility: visible;
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
} 

.creditsSoMe{
    display: flex;
    justify-content: center;
    text-align: center;
}

.creditsSoMe img{
    width: 2.5rem;
    -webkit-filter: invert(1);
            filter: invert(1); 
    margin-top: 2rem;
    padding: 0.5rem;
}

@media only screen and (max-width: 1024px){
    .header img{
        width: 18rem;
    }

    .footer{
        padding: 2rem 0 0 0;
    }

    .footer-columns{
        margin: auto;
        padding: 2rem 0rem;
        width: 50vw;
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }

    .credits h5{
        font-size: .8rem;
        text-align: center;
    }

}

@media only screen and (max-width: 600px){
    .header img{
        width: 10rem;
        left: 1rem;
    }

    .desktopMenu {
        display: none;
        opacity: 0;
    }

    .headerMobile{
        display: inline;
        display: initial;
    }
    .mobileToggleMenuBtn{
        opacity: 1;
        position: absolute;
        z-index: 5;
        width: 3rem;
        height: 2.5rem;
        top: 0.5rem;
        right: 1.2rem;
        fill: white;
        -webkit-transition: opacity 300ms; 
        transition: opacity 300ms;
        background-color: transparent;
    }

    .mobileToggleMenuBtn button{
        background-color: transparent;
        border: none;
    }

    .mobileMenu {
        opacity: 0;
        position: fixed;
        z-index: -1;
        top: 0;
        margin: 0;
        width: 100vw;
        height: 100vh;
        background-color: #a7a7a7;
        text-align: center;
        -webkit-transition: opacity 300ms; 
        transition: opacity 300ms;
    }

    .mobileExitBtn{
        position: fixed;
        top: 1rem;
        right: 1rem;
        fill: white;
    }

    .mobileExitBtn button{
        background-color: transparent;
        border: none;
    }

    .mobileMenuList {
        position: relative;
        top: 50%;
        -webkit-transform: translateY(-50%);
        transform: translateY(-50%);
    }

    .mobileMenuList h2 {
        color: white;
        -webkit-text-decoration-line: underline;
                text-decoration-line: underline;
    }

    .mobileMenuList a {
        color: white;
        text-decoration: none;
        text-transform: uppercase !important;
        font-size: 1.5rem;
        display: block;
    }

    .nav-link--active{
        color: white !important;
        font-weight: 400 !important;
    }
}
